import React, { Component } from "react";
import { formatPrice } from "../js/utilities";

class Food extends Component {
  constructor(props) {
    super(props);
    this.state = {
      count: 0
    };

    this.handleClickIncrement = this.handleClickIncrement.bind(this);
    this.handleClickDecrement = this.handleClickDecrement.bind(this);
  }

  addToCart = (price, name) => {
    const item = {
      price: price,
      name: name,
      count: this.state.count
    };
    this.props.addFood(this.props.index, item);
  };

  handleClickIncrement = event => {
    const price = event.currentTarget.value;
    const name = event.currentTarget.name;
    this.setState({ count: this.state.count + 1 }, () =>
      this.addToCart(price, name)
    );
    let cost = price * 1;
    this.props.setTotal(cost);
  };

  handleClickDecrement = event => {
    if (this.state.count > 0) {
      const price = event.currentTarget.value;
      const name = event.currentTarget.name;
      this.setState({ count: this.state.count - 1 }, () =>
        this.addToCart(price, name)
      );
      let cost = price * -1;
      this.props.setTotal(cost);
    }
  };

  render() {
    const { image, name, desc, price } = this.props.details;
    // let removeFromCart;
    // if (this.state.count > 0) {
    //   removeFromCart = (
    //     <button
    //       type="button"
    //       value={price}
    //       name={name}
    //       onClick={this.handleClickDecrement}
    //     >
    //       {" "}
    //       Remove from Cart
    //     </button>
    //   );
    // } else {
    //   removeFromCart = <div />;
    // }
    return (
      <li key="this.props.key" className="{status}">
        <div className="itemCard">
          <img className="itemImg" src={image} alt={name} />
          <h2 className="itemName">{name}</h2>
          <p className="desc">{desc}</p>
          <p className="price">{formatPrice(price)}</p>
        </div>

        <button
          className="buttonAdd"
          type="button"
          value={price}
          name={name}
          onClick={this.handleClickIncrement}
        >
          {" "}
          add to cart
        </button>
        {/* {removeFromCart} */}
        {/* <h2>
          {name} {status}
        </h2>
        <p>{price}</p>
        <p>{desc}</p> */}
      </li>
    );
  }
}

export default Food;
