import React, { Component } from "react";
import Food from "./Food";
import foods from "../js/foods.js";
import Inventory from "./Inventory";
import base from "../js/base";
import Sidebar from "./Sidebar";
import Button from "./Button";

class App extends Component {
  state = {
    foods: [],
    list: [],
    total: 0
  };

  componentDidMount() {
    this.ref = base.syncState("foods", {
      // Firebase needs a couple pieces of info
      context: this,
      state: "foods"
    });
  }

  loadFoods = () => {
    this.setState({ foods });
  };

  updateFood = (key, updatedFood) => {
    const foods = { ...this.state.foods };
    foods[key] = updatedFood;
    this.setState({ foods });
  };

  setTotal = cost => {
    this.setState({ total: this.state.total + cost });
  };

  deleteFood = () => {
    console.log("delete Food");
  };

  addFood = (key, addFood) => {
    console.log("it werk");
    const list = { ...this.state.list };
    list[key] = addFood;
    this.setState({ list });
  };

  render() {
    return (
      <>
        <div className="app">
          <section className="admin">
            <Button loadFoods={this.loadFoods} />
            <Inventory
              updateFood={this.updateFood}
              deleteFood={this.deleteFood}
              foods={this.state.foods}
              addToCart={this.addFood}
            />
          </section>

          <ul className="foods">
            <div className="itemContainer">
              {Object.keys(this.state.foods).map(key => (
                <Food
                  key={key}
                  index={key}
                  details={this.state.foods[key]}
                  addFood={this.addFood}
                  setTotal={this.setTotal}
                />
              ))}
            </div>
          </ul>
          <section className="cartSection">
            <h4>Cart</h4>
            <React.Fragment>
              <Sidebar
                list={this.state.list}
                total={this.state.total}
                addFood={this.addFood}
                setTotal={this.state.setTotal}
              />
            </React.Fragment>
          </section>
        </div>
      </>
    );
  }
}

export default App;
