import React, { Component } from "react";
import { formatPrice } from "../js/utilities";

// import Button from "./Button";

class CartItem extends Component {
  remove = event => {
    console.log(event.currentTarget.value);
  };

  addToCart = (price, name, newcount) => {
    const item = {
      price: price,
      name: name,
      count: newcount
    };
    this.props.addFood(this.props.index, item);
  };

  handleClickIncrement = event => {
    const price = event.currentTarget.value;
    const name = event.currentTarget.name;
    const newcount = event.currentTarget.count + 1;
    this.addToCart(price, name, newcount);
    let cost = price * 1;
    this.props.setTotal(cost);
  };

  handleClickDecrement = event => {
    const price = event.currentTarget.value;
    const name = event.currentTarget.name;
    const newcount = event.currentTarget.count - 1;
    this.addToCart(price, name, newcount);
    let cost = price * -1;
    this.props.setTotal(cost);
  };
  render() {
    const { name, price, count } = this.props.details;
    return (
      <li key={this.props.index}>
        <h3 className="cartItemQuantity">{count}</h3>
        <h3 className="cartItemName">{name}</h3>
        <h3 className="cartItemPrice">{formatPrice(price)}</h3>

        {/* <button
            type="button"
            value={price}
            name={name}
            count={count}
            onClick={this.handleClickIncrement}
          >
            {" "}
            add to cart
          </button>
          <button
            type="button"
            value={price}
            name={name}
            count={count}
            onClick={this.handleClickDecrement}
          >
            {" "}
            remove from cart
          </button> */}
      </li>
    );
  }
}

export default CartItem;
